import React, { useEffect } from "react";
import { Divider } from "antd";
import styled from "styled-components";
import Flower from "../assets/flower2.png";
import Reception from "../assets/CheorwonReception.webp"

const Wrapper = styled.div`
  padding-top: 42px;
  width: 70%;
  margin: 0 auto;
`;

const Title = styled.span`
  font-size: 1rem;
  color: var(--title-color);
  font-weight: bold;
  opacity: 0.85;
  margin-bottom: 0;
`;

const Image = styled.img`
  display: block;
  margin: 0 auto;
  width: 1.375rem;
  padding-bottom: 42px;
`;

const Content = styled.p`
  font-size: 0.875rem;
  line-height: 1.75;
  opacity: 0.75;
  width: 100%;
  text-align: center;
  padding-top: 42px;
  padding-bottom: 42px;
  margin: 0;
`;

const Map = styled.div`
  width: 100%;
  padding: 0;
`;

const BusItem = styled.span`
  font-weight: bold;
  padding: 2px 6px;
  border-radius: 4px;
  color: white;
  background-color: ${({ busType }) =>
    busType === "" ? "blue" :
    busType === "" ? "green" :
    busType === "" ? "orange" :
    busType === "" ? "red" :
    busType === "" ? "purple" :
    "black"};
`;

const SubwayLine = styled.span`
  font-weight: bold;
  padding: 2px 6px;
  border-radius: 4px;
  color: white;
  background-color: ${({ line }) =>
    line === "2" ? "#33A23D" : // 2 ()
    line === "7" ? "#6D8EB5" : // 7 ()
    "black"};
`;

const CheorwonReception = styled.img`
  width: 100%;
  display: block;
  `;

const Location = () => {
  //   
  const executeScript = () => {
    const scriptTag = document.createElement("script");
    const inlineScript = document.createTextNode(`new daum.roughmap.Lander({
    "timestamp" : "1740736481162",
    "key" : "2n7n8",
    "mapWidth" : "640",
    "mapHeight" : "360"
  }).render();`);
    scriptTag.appendChild(inlineScript);
    document.body.appendChild(scriptTag);
  };

  const InstallScript = () => {
    (function () {
      let c = window.location.protocol === "https:" ? "https:" : "http:";
      let a = "16137cec";

      if (window.daum && window.daum.roughmap && window.daum.roughmap.cdn) {
        return;
      }
      window.daum = window.daum || {};
      window.daum.roughmap = {
        cdn: a,
        URL_KEY_DATA_LOAD_PRE: c + "//t1.daumcdn.net/roughmap/",
        url_protocal: c,
      };
      let b =
        c +
        "//t1.daumcdn.net/kakaomapweb/place/jscss/roughmap/" +
        a +
        "/roughmapLander.js";

      const scriptTag = document.createElement("script");
      scriptTag.src = b;
      document.body.append(scriptTag);
      scriptTag.onload = () => {
        executeScript();
      };
    })();
  };

  useEffect(() => {
    InstallScript();
  }, []);

  return (
    <Wrapper>
      <Divider plain style={{ marginTop: 0, marginBottom: 32 }}>
        <Title> </Title>
      </Divider>
      <Image src={Flower} />
      
      <Map
        id="daumRoughmapContainer1740736481162"
        className="root_daum_roughmap root_daum_roughmap_landing"
      ></Map>
      <Content>
        <b>   87
        <br />
          6F  </b>
        <br />
        <br />
        <Title> </Title>
        <br />
        <br />
        <b>   </b>[05218]
        <br />
        <BusItem busType=""></BusItem> 240, 721<br />
        <BusItem busType=""></BusItem> 2222, 2224<br />
        <BusItem busType=""></BusItem> 3500<br />
        <BusItem busType=""></BusItem> 6013<br />
        <BusItem busType=""></BusItem> 05
        <br />
        <br />
        <Title> </Title>
        <br />
        <br />
        <SubwayLine line="2">2</SubwayLine>, <SubwayLine line="7">7</SubwayLine>  5    ( 30m)
        <br />
        <br />
        <Title> </Title>
        <br />
        <br />
        : ""  " " 
        <br />
         : "   87"  "   2-2" 
        <br />
        <br />
        <Title> </Title>
        <br />
        <br />
        <CheorwonReception src={Reception} alt="Cheorwon Reception"></CheorwonReception>
      </Content>
    </Wrapper>
  );
};

export default Location;